<template>
    <iscroll-view  ref="scrollView" class="scroll-view msg-items" :options="{click:true, scrollY: true, mouseWheel: true, probeType: 1}"  :scrollerClass="{'scroller': true}"  @scroll="scrolling" @pullUp="scrollEnd">
      <div v-for="item in msgs">
        <div class="date-separation-area" v-if="item.isFirstDisplayDate"> {{ item.push_time  | moment('YYYY-MM-DD')}}</div>
        <div class="separation-area" v-else></div>
        <div class="msg-item" >
          <a class="title" v-html="item.title" :href="'/#/msg-detail?msgId=' + item.id + '&type=' + item.type_en_name + '&subType=' + item.sub_type_en_name"></a>
          <div class="company-name" v-html="item.company"></div>
          <div class="bottom-line">
            <div class="type" v-html="item.type_cn_name"></div>
            <div class="right-part">
              <div class="collect-icon " v-bind:class="item.collected?'icon-has-collect': 'icon-not-collect'" @click="setCollect(item)"></div>
              <div class="date">{{item.push_time | moment('YYYY-MM-DD')}}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="data-load-tips">{{tips}}</div>
    </iscroll-view>
</template>

<script>
  import {Toast} from 'mint-ui'
  import moment from 'moment'
  export default{
    props: ['totalCount', 'msgs', 'pullUp'],
    created () {
    },
    updated () {
      this.refresh()
    },
    data () {
      return {
        tips: ''
      }
    },
    watch: {
      msgs: function (newVal, oldVal) {
        let dateObj = {}
        let date = ''
        newVal.forEach(function (item) {
          date = moment(item.push_time).format('YYYY-MM-DD')
          if (!dateObj[date]) {
            dateObj[date] = true
            item.isFirstDisplayDate = true
          }
        })
        if (this.msgs.length < this.totalCount) {
          this.tips = '上拉加载更多'
        } else {
          this.tips = '没有更多数据~'
        }
      }
    },
    methods: {
      refresh () {
        var self = this
        console.log(self.$refs.scrollView)
        self.$refs.scrollView.refresh()
      },
      scrolling () {
        var self = this
        if (self.msgs.length < self.totalCount) {
          self.tips = '松手开始加载...'
        } else {
          self.tips = '没有更多数据~'
        }
        console.log(self.tips)
      },
      scrollEnd () {
        var self = this
        if (self.msgs.length < self.totalCount) {
          self.tips = '上拉加载更多'
        } else {
          self.tips = '没有更多数据~'
        }
        console.log(self.$refs.scrollView.iscroll.y)
        self.pullUp()
      },
      setCollect (item) {
        var ids = []
        var collect = !item.collected
        ids.push(item.id)
        fetch('/api/notification/set_collect', {
          method: 'PATCH',
          headers: {
            'Authorization': 'Bearer ' + localStorage.getItem('access_token'),
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            ids: ids,
            collect: collect
          })
        }).then(function (response) {
          return response.json()
        }).then(function (data) {
          if (data.status === 0) {
            if (collect) {
              Toast('收藏成功')
            } else {
              Toast('取消收藏成功')
            }
            item.collected = collect
          }
        }).catch(function (e) {
          Toast('error')
        })
      }
    }
  }
</script>
<style lang="scss">
  .msg-items {
    position: absolute;
    z-index: 1;
    top: 85px;
    bottom: 0;
    width: 100%;
    overflow: hidden;
    background-color: #F7F7F7;
    .scroller{
    }
    .msg-item {
      background: #fff;
      padding: 14px 12px 16px 14px;
      border-top: 1px solid #EFEFEF;
      border-bottom: 1px solid #EFEFEF;

      .title {
        color: #333;
        font-size: 16px;
      }

      .company-name {
        margin-top: 6px;
        color: #999;
        font-size: 14px;
      }

      .bottom-line {
        display: flex;
        justify-content: space-between;
        margin-top: 16px;
        align-items: center;

      .type {
        float: left;
        display: inline-block;
        background: #F0F0F0;
        padding: 6px 9px;
        border-radius: 100px;
        color: #2178D4;
        font-size: 12px;
      }
      .right-part {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;

        .date {
          float: right;
          margin-right: 10px;
          color: #BDBDBD;
          font-size: 12px;
        }

        .collect-icon {
          float: right;
          background-size: 24px auto;
          width: 24px;
          height: 24px;
        }

        .icon-has-collect {
          background-image: url(../assets/img/has_collect.png);
        }

        .icon-not-collect {
          background-image: url(../assets/img/not_collect.png);
        }

        }
      }
    }
    .data-load-tips{
      padding: 15px 0 10px 0;
      text-align: center;
      color: #999;
      font-size: 12px;
    }
    .date-separation-area{
      height: 28px;
      line-height: 28px;
      text-align: center;
      font-size: 12px;
      color: #BDBDBD;
    }
    .separation-area{
      height: 10px;
    }
  }

</style>



