<template>
    <div class="login-page">
        <div class="logo-area"></div>
        <input class="phone-input" type="text" placeholder="手机号" v-model="phone">
        <input class="pass-input" type="password" placeholder="密码" v-model="password">
        <div class="validation-tips">6-20位字符，区分大小写</div>
        <div class="login-btn" @click="login">登录</div>
    </div>
</template>

<script>
  import fetch from 'isomorphic-fetch'
  import { Toast } from 'mint-ui'
  export default{
    data () {
      var phone
      var password
      return {
        phone: phone,
        password: password
      }
    },
    methods: {
      login () {
        var self = this
        fetch('/api/account/user/login', {method: 'POST',
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            username: self.phone,
            password: self.password
          })
        }).then(function (response) {
          return response.json()
        }).then(function (data) {
          if (!data.access_token) {
            Toast({
              message: data.msg,
              iconClass: 'icon icon-error',
              duration: 2000
            })
            return
          }
          localStorage.setItem('access_token', data.access_token)
          Toast({
            message: '登录成功',
            iconClass: 'icon icon-success',
            duration: 2000
          })
          window.location.href = '/#/risk-msg-list'
        }).catch(function (e) {
        })
      }
    }
  }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
    ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
        color:  #BDBDBD;
    }
    :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
        color:  #BDBDBD;
        opacity:  1;
    }
    ::-moz-placeholder { /* Mozilla Firefox 19+ */
        color:  #BDBDBD;
        opacity:  1;
    }
    :-ms-input-placeholder { /* Internet Explorer 10-11 */
        color:  #BDBDBD;
    }
    .login-page{
        width: 309px;
        margin: 0 auto;
        text-align: center;
        .logo-area{
            display: inline-block;
            background: url(../assets/img/bank_logo.png);
            background-size: 169px auto;
            width: 169px;
            height: 50px;
            margin-top: 62px;
        }
        .phone-input, .pass-input{
            box-sizing: border-box;
            width: 309px;
            height: 44px;
            padding-left: 16px;
            border: 1px solid #BDBDBD;
            font-size: 14px;
        }
        .phone-input{
            margin-top: 110px;
        }
        .pass-input{
            margin-top: 20px;
        }
        .validation-tips{
            width: 100%;
            margin-top: 8px;
            font-size: 12px;
            color: #BDBDBD;
            text-align: right;
        }
        .login-btn{
            display: inline-block;
            box-sizing: border-box;
            background: #2178D4;
            width: 169px;
            height: 42px;
            line-height: 42px;
            border-radius: 2px;
            margin-top: 41px;
            color: #fff;
            font-size: 16px;
        }
    }
</style>



